}

.Topbar_links__3cIHK li a, .Topbar_name__3IQ8L, .Topbar_dropdown-btn__s14K_ { 
    transition: all 0.3s ease 0s;
}

.Topbar_link__35CWg {
    position: relative;
}

.Topbar_link__35CWg:hover {
    color: #ccc;
}

.Topbar_link__35CWg::before {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 0;
    height: 1px;
    background-color: #ccc;
    transition: width 0.6s cubic-bezier(0.25, 1, 0.5, 1);
}

@media (hover: hover) and (pointer: fine) {
    .Topbar_link__35CWg:hover::before{
      left: 0;
      right: auto;
      width: 100%;
    }
  }

.Topbar_dropdown-btn__s14K_ {
    color: #DFDFDF;
    font-size: 40px;
    font-weight: 100;
}

.Topbar_dropdown-btn__s14K_:hover {
    color: #5F5980;
    cursor: pointer;
}

